<template>
  <div>
    <users-select @item-selected="fetchUser" />

    <b-card no-body class="col-12">
      <div v-if="loading" class="text-center mt-5 mb-5">
        <i class="fa fa-circle-o-notch fa-spin fa-fw view__loader fa-lg"></i>
        <br />Loading
      </div>

      <b-row class="mt-3" v-if="!loading">
        <b-col md="6" class="my-1">
          <b-form-group horizontal label="Per page" class="mb-0">
            <b-form-select
              :options="pageOptions"
              v-model="perPage"
              @change="(val) => setPerPage(val)"
            />
          </b-form-group>
        </b-col>

        <b-col md="6" sm="6" class="my-2">
          <b-form-group horizontal label="Sort" class="mb-0">
            <b-input-group>
              <b-form-select v-model="sortBy" :options="sortOptions">
                <option slot="first" :value="null">-- none --</option>
              </b-form-select>

              <b-form-select :disabled="!sortBy" v-model="sortDesc" slot="append">
                <option :value="false">Asc</option>
                <option :value="true">Desc</option>
              </b-form-select>
            </b-input-group>
          </b-form-group>
        </b-col>
      </b-row>

      <b-table
        v-if="!loading && items.length > 0"
        :hover="hover"
        :striped="striped"
        :bordered="bordered"
        :small="small"
        :fixed="fixed"
        :filter="filter"
        :filtered="onFiltered"
        :items="items"
        :fields="fields"
        :per-page="perPage"
        :sort-by.sync="sortBy"
        :sort-desc.sync="sortDesc"
        class="mt-3"
        responsive="sm"
      >
        <template v-slot:cell(username)="data">
          <span class="badge badge-primary" v-if="data.item.user">
            <strong>{{data.item.user ? (data.item.user.username) : (data.item.temp_user.username)}}</strong>
          </span>
          <span
            class="badge badge-primary"
            v-if="data.item.temp_user"
          >{{data.item.user ? (data.item.user.username) : (data.item.temp_user.username)}} - Temporary User</span>
        </template>

        <template v-slot:cell(action)="data">
          <button class="btn btn-sm btn-dark ml-1" @click="openDetails(data.item)">
            Details
            <i class="fa fa-plus" />
          </button>

          <button @click="deleteNode(data.item)" class="btn btn-sm btn-danger ml-1">
            Delete
            <i class="fa fa-times" />
          </button>
        </template>
      </b-table>

      <nav>
        <paginate
          :page-count="pageCount"
          :page-range="3"
          :click-handler="nextPageCall"
          :prev-text="'Prev'"
          :next-text="'Next'"
          :container-class="'pagination b-pagination pagination-md'"
          :prev-class="'page-item'"
          :prev-link-class="'page-link'"
          :next-class="'page-item'"
          :next-link-class="'page-link'"
          :page-class="'page-item'"
          :page-link-class="'page-link'"
        ></paginate>
      </nav>
    </b-card>
    <details-modal></details-modal>
  </div>
</template>

 

<script>
import _ from "lodash";
import parseLink from "parse-link-header";
import Paginate from "vuejs-paginate";

import { serverBus } from "@/main";
import DetailsModal from "./Details";
import UsersSelect from "@/components/UsersSelect";

export default {
  name: "scanners-list",
  props: {
    server: { type: String },
    hover: {
      type: Boolean,
      default: true
    },
    striped: {
      type: Boolean,
      default: true
    },
    bordered: {
      type: Boolean,
      default: true
    },
    small: {
      type: Boolean,
      default: true
    },
    fixed: {
      type: Boolean,
      default: false
    }
  },
  components: {
    Paginate,
    DetailsModal,
    UsersSelect
  },
  data() {
    return {
      items: [],
      fields: [
        { key: "id", sortable: true, label: "ID" },
        { key: "username" },
        { key: "account" },
        { key: "server" },
        {
          key: "created_at",
          label: "Created",
          sortable: true,
          thClass: "text-right",
          tdClass: "text-right"
        },
        {
          key: "action",
          label: "Action",
          thClass: "text-right",
          tdClass: "text-right"
        }
      ],
      filter: null,
      loading: false,
      pageOptions: [5, 10, 15, 20, 25, 30, 40, 50, 100, 250, 500, 1000],
      sortBy: "status",
      sortDesc: false,
      totalNodes: 0,
      pageCount: 1,
      perPage: 100,
      options: [],
      page: 1
    };
  },
  computed: {
    sortOptions() {
      return this.fields
        .filter(f => f.sortable)
        .map(f => {
          return { text: f.label, value: f.key };
        });
    }
  },
  created() {
    this.fetchNodes();
    serverBus.$on("scanner-fetch-nodes", e => {
      this.fetchNodes();
    });
  },
  beforeDestroy() {
    serverBus.$off("scanner-fetch-nodes");
  },
  watch: {
    "$route.query.status"() {
      this.fetchNodes();
    }
  },
  methods: {
    setPerPage(val) {
      this.perPage = val;
      this.fetchNodes();
    },
    fetchUser(username) {
      this.loading = true;
      this.$http(
        "/api/v1/admin/scanners?username=" + username + "&per_page=1000&page=1",
        {
          headers: {
            "content-type": "application/json",
            Authorization: this.$store.getters.authToken
          }
        }
      )
        .then(response => {
          this.items = response.data;
          this.totalNodes = response.data.length;
          this.perPage = response.data.length;
          this.pageCount = 1;
          this.loading = false;
        })
        .catch(error => {
          error.response.data.map(m => {
            this.$toasted.show(m, {
              position: "top-right",
              duration: 5000,
              type: "error",
              closeOnSwipe: true
            });
            this.loading = false;
          });
        });
    },
    nextPageCall(pageNum) {
      this.fetchNodes(pageNum);
    },
    fetchNodes(pageNo = 1) {
      this.loading = true;
      this.$http(
        "/api/v1/admin/scanners?per_page=" + this.perPage + "&page=" + pageNo,
        {
          headers: {
            "content-type": "application/json",
            Authorization: this.$store.getters.authToken
          }
        }
      )
        .then(response => {
          this.items = response.data;
          this.totalNodes = parseInt(response.headers.total);
          this.perPage = parseInt(response.headers["per-page"]);
          this.pageCount = Math.ceil(
            parseFloat(
              response.headers["per-page-total"] /
                parseInt(response.headers["per-page"])
            )
          );
          this.loading = false;
        })
        .catch(error => {
          error.response.data.map(m => {
            this.$toasted.show(m, {
              position: "top-right",
              duration: 5000,
              type: "error",
              closeOnSwipe: true
            });
            this.loading = false;
          });
        });
    },
    deleteNode(item) {
      if (config.validateDelete()) {
        this.loading = true;
        this.$http({
          url: "/api/v1/admin/scanners/" + item.id,
          method: "DELETE",
          headers: {
            "content-type": "application/json",
            Authorization: this.$store.getters.authToken
          }
        })
          .then(response => {
            this.$toasted.show("Scanner Destroyed", {
              position: "top-right",
              duration: 5000,
              type: "danger",
              closeOnSwipe: true
            });
            this.loading = false;
            this.fetchNodes();
          })
          .catch(error => {
            error.response.data.map(m => {
              this.$toasted.show(m, {
                position: "top-right",
                duration: 5000,
                type: "error",
                closeOnSwipe: true
              });
            });
            this.loading = false;
          });
      }
    },
    openDetails(item) {
      serverBus.$emit("scanner-details-modal-open", item);
    },
    onFiltered(filteredItems) {
      this.totalRows = filteredItems.length;
      this.currentPage = 1;
    }
  }
};
</script>
