<template>
  <b-modal size="lg" id="scanner-modal-details" :title="'Scanner - #' + node.id">
    <form >
      <div slot="default">
        <div class="form-group row mt-5">
          <label for="colFormLabelSm" class="col-sm-2">First Name</label>
          <div class="col-sm-10">
            <input :readonly="!editMode" v-model="node.first_name" class="form-control" />
          </div>
        </div>
        <div class="form-group row">
          <label for="colFormLabel" class="col-sm-2">Last Name</label>
          <div class="col-sm-10">
            <input :readonly="!editMode" v-model="node.last_name" type="text" class="form-control" />
          </div>
        </div>

        <div class="form-group row">
          <label for="colFormLabel" class="col-sm-2">Email</label>
          <div class="col-sm-10">
            <input :readonly="!editMode" v-model="node.email" type="email" class="form-control" />
          </div>
        </div>

        <div class="form-group row">
          <label for="colFormLabel" class="col-sm-2">Phone Number</label>
          <div class="col-sm-10">
            <input
              :readonly="!editMode"
              v-model="node.phone"
              type="text"
              class="form-control"
              placeholder="E.g: +19543223455"
            />
          </div>
        </div>

        <div class="form-group row">
          <label for="colFormLabelLg" class="col-sm-2">Account</label>
          <div class="col-sm-10">
            <input :readonly="!editMode" v-model="node.account" type="text" class="form-control" />
          </div>
        </div>

        <div class="form-group row">
          <label for="colFormLabelLg" class="col-sm-2">Server</label>
          <div class="col-sm-10">
            <input :readonly="!editMode" v-model="node.server" type="text" class="form-control" />
          </div>
        </div>
      </div>
    </form>
    
    <template v-slot:modal-footer>
      <template v-if="editMode">
        <button type="submit" @click.prevent="updateNode" class="btn mr-2 btn-success" :disabled="loading">Save <i class="ml-1 fa fa-save"></i></button>
      </template>
      <template v-else>
        <button class="btn mr-2 btn-warning" @click="editMode = true">Edit <i class="ml-1 icon-pencil"></i> </button>
      </template>
      <button type="button" class="btn btn-dark" data-dismiss="modal" @click="hideModal()">Close</button>
    </template>
  </b-modal>
</template>

<script>
import { serverBus } from "@/main";

export default {
  name: "scanner-details-modal",
  data() {
    return {
      loading: false,
      editMode: false,
      show: true,
      node: {
        id: null,
        first_name: "",
        last_name: "",
        email: "",
        account: "",
        server: "",
        phone: ""
      }
    };
  },
  created() {
    serverBus.$on("scanner-details-modal-open", node => {
      this.node = node;
      this.showModal();
    });
  },
  beforeDestroy() {
    serverBus.$off("scanner-details-modal-open");
  },
  methods: {
    showModal() {
      this.$root.$emit("bv::show::modal", "scanner-modal-details");
    },
    hideModal() {
      this.$root.$emit("bv::hide::modal", "scanner-modal-details");
    },
    updateNode() {
      console.log("sub");
      this.loading = true;
      this.$http({
        url: "/api/v1/admin/scanners/" + this.node.id,
        data: this.node,
        method: "PUT",
        headers: {
          "content-type": "application/json",
          Authorization: this.$store.getters.authToken
        }
      })
        .then(response => {
          this.$toasted.show("Scanner Updated", {
            position: "top-right",
            duration: 5000,
            type: "default",
            closeOnSwipe: true
          });
          this.loading = false;
          this.editMode = false;
          serverBus.$emit("scanner-fetch-nodes");
        })
        .catch(error => {
          this.loading = false;
          error.response.data &&
            error.response.data.map(m => {
              this.$toasted.show(m, {
                position: "top-right",
                duration: 5000,
                type: "error",
                closeOnSwipe: true
              });
            });
        });
    }
  }
};
</script>
